$main_bg: #e4dfcb;
$content_bg: #FDEDDE;
// $content_bg: #e4dccb;
// $main_bg: #FCECDD;


body {
    background: $main_bg;
    font-family :'Times New Roman', Times, serif;
    overflow: hidden;
    font-size: $font-size-body;
}

h2 {
    font-family: 'Atteron', serif;
    font-size: $font-size-title;
}

small {
    font-weight: 100;
}

// .languages {
//     display: none;
// }

#wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 1rem;
}

#content, #header {
    background: $content_bg;
}

#header {
    border-radius: 50% 50%/200px 200px 0 0;
    padding: 2rem 2rem 0 2rem;
    margin-top: 2rem;
}

.intro {
    padding: 0 1.5rem;
    padding-top: 2rem;
    font-size: $font-size-subtitle;
    text-align: center;
    font-style: italic;
}

.legend {
    padding: 2rem 4rem 0 4rem;
    font-size: $font-size-subtitle;
    font-weight: bold;
}

#content {
    border-radius: 0 0 20px 20px;
    flex-grow: 1;
}

.center-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-link {
    max-width: 8rem;
    opacity: 0.7;
}

.header-img {
    width: 90%;
}

.section-title {
    padding: 1.5rem 0;
    text-align: center;
}

.section-description {
    padding: 0 1rem 2rem 1rem;
    font-size: $font-size-subtitle;
    text-align: center;
    font-style: italic;
}

.grid {
    margin-left: auto;
    margin-right: auto;
    display: grid;
    padding: 0 1rem;
}

.card {
    padding: 1rem 0.5rem;
    display: flex;
    flex-direction: column;
}

.infos {
    flex-grow: 1;
}

.image {
    text-align: center;
    padding: 1rem;
    width: 100%;
    img {
        width: 50%;
        margin: auto;
        aspect-ratio: 1 / 1;
        border-radius: 8%;
    }
}

.image:empty {
    display: none;
}

.title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: $font-size-subtitle;
    font-weight: bold;
}

.small-text {
    opacity: 0.9;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    font-style: italic;
}

.option-list {
    display: flex;
    margin-top: 0.5rem;
    column-gap: 0.5rem;
    font-size: $font-size-small;
}

.option {
    // border: 1px solid $primary;
    padding: 0.3rem 0.5rem;
    border-radius: 0.25rem;
    background-color: rgba(136, 142, 128, 0.15);
    font-weight: bold;
}

.choice {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
}

.dots {
    border-style: solid;
    border-width: 0 0 2px;
    border-color: #000;
    border-bottom-style: dotted;
    flex-grow: 1;
    margin: 0 4px 0;
    opacity: 0.2;
}

#footer {
    padding: 2rem;
}

@media (min-width: 768px) {

    #header {
        border-radius: 50% 50%/350px 350px 0 0;
          .header-link {
            max-width: 10rem;
        }
    }

    .intro {
        padding: 2rem 8rem 0rem 8rem;
        font-size: $font-size-subtitle;
    }

    #wrapper {
        margin: 0 4rem;
    }

    .legend {
        padding-left:8rem;
    }

    #content {
        padding:0 2rem;
        .md\:grid-cols-2 {
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }

        .md\:grid-cols-3 {
            grid-template-columns: repeat(3, minmax(0, 1fr));
        }

        .grid {
            padding: 0;
        }

        .card {
            flex-direction: row;

            .infos {
                flex-grow: 1;
                width: 100%;
            }

            .image {
                padding: 0 1rem;
                flex-grow: 1;
                width: 50%;
                max-width: 180px;
                img {
                    width: 100%;
                }
            }
        }
    }

}
